.page-client-optive{
    .eco{
        background: url('../../img/eco-bg-optive.png');
        &__wrapper{
            padding-top: 229px;
        }
        &__title {
            font-family: "Raleway";
            font-style: normal;
            font-weight: 800;
            font-size: 45px;
            line-height: 53px;
            letter-spacing: 0.01em;
            color: #FFFFFF;
            margin-top: 20px;
            letter-spacing: 0;
            margin-left: -20px;
        }
        &__subtitle {
            font-family: "Raleway";
            font-style: normal;
            font-weight: 500;
            font-size: 22px;
            line-height: 28px;
            text-align: center;
            letter-spacing: 0.01em;
            color: #FFFFFF;
            width: 660px;
            margin-top: 25px;
            letter-spacing: -0.09px;
            margin-left: 10px;
            line-height: 30px;
        }
        
    }
    .contact__wrapper{
        padding-top: 32px;
        padding-bottom: 60px;
    }
    .info{
        margin-top: 158px;
    
    }
    .info__item:last-child .info__subtitle {
        margin-left: 13px;
      
    }
    .payment__list{
        padding: 0 10px;
        margin-top: 109px;
    }
    .payment__nav-title {
        width: 147px;
    }
    .payment__item{
        padding-bottom: 15px;
        width: 203px;
        margin-top: 5px;
    }
     .payment__item:nth-child(2){
        padding-bottom: 15px;
        width: 198px;
        margin-top: 1px;
        margin-left: 13px;
    }
    .payment__item:nth-child(3) {
        padding-bottom: 15px;
        width: 203px;
        margin-top: -11px;
        margin-right: 77px;
        svg{
            margin-left: -11px;
            margin-top: 3px;

        }
        .payment__nav-title {
       margin-top: 10px;
        }
    }
    .payment__item:nth-child(4) {
        padding-bottom: 15px;
        width: 211px;
        margin-top: 5px;
        margin-left: -91px;
        margin-right: 17px;
        svg{
            margin-left: -2px;
            margin-top: -7px;
        }
    }
   .payment__item:last-child {
        padding-bottom: 15px;
        width: 203px;
        margin-top: 5px;
        margin-right: -4px;
        svg{
            margin-left: -9px;
            margin-top: -8px;
        }
        }
   

     
    .title__video{
        background: url('../../img/optiveTitle-video-bg.png');
        video{
            display: none;
        }
        img{
            display: none;
        }
        .title__video-subtitle {
            font-family: "Raleway";
            font-style: normal;
            font-weight: 600;
            font-size: 25px;
            line-height: 30px;
            text-align: center;
            letter-spacing: 0.06px;
            color: #FFFFFF;
            width: 540px;
            margin-bottom: 40px;
            margin-left: -5px;
        }  
        .title__video-title {
            font-family: "Raleway";
            font-style: normal;
            font-weight: 800;
            font-size: 62px;
            line-height: 73px;
            letter-spacing: 0.01em;
            color: #FFFFFF;
            margin-bottom: 32px;
            margin-top: 9px;
        }
        .content{
        margin-top: 76px;
        }
   
    }
    .info__subtitle{
        margin-left: -2px;
        margin-top: 5px;
    }
    .info__item:nth-child(2){
        margin-left: 18px;
        .info__subtitle {
        margin-left: 2px;
        margin-top: 5px;
    } 
    }
    .info__item:nth-child(3){
        margin-left: 58px;
    } 
    .info__item:nth-child(4) {
        margin-right: 1px;
    }
    .info__item:nth-child(3) .info__subtitle {
        width: 317px;
        margin-left: -4px;
        letter-spacing: -0.01px;
        margin-top: 6px;
    }
 }